import React from "react";
import { Row, Card, Col, Container } from "react-bootstrap";

const WazirxFeatures = () => {
  const featuresList = [
    {
      number: 1,
      title: "KYC/AML Verification",
      description:
        "Ensure regulatory compliance and bolster security with our integrated Know Your Customer (KYC) and Anti-Money Laundering (AML) verification processes.",
    },
    {
      number: 2,
      title: "Multi-Currency Support",
      description:
        "Empower your exchange with the versatility to trade multiple cryptocurrencies, offering users a diverse range of options to diversify their portfolios.",
    },
    {
      number: 3,
      title: "IEO Launchpad",
      description:
        "Ignite innovation and growth by offering an Initial Exchange Offering (IEO) launchpad, attracting promising blockchain ventures to your platform.",
    },
    {
      number: 4,
      title: "Atomic Swaps",
      description:
        "Enable direct peer-to-peer trading between different cryptocurrencies with atomic swaps, eliminating the need for intermediaries and enhancing decentralization.",
    },
    {
      number: 5,
      title: "Real-Time Pricing",
      description:
        "Keep your users informed with live updates on cryptocurrency prices, ensuring they make well-informed trading decisions based on the latest market data.",
    },
    {
      number: 6,
      title: "Efficient Dispute Resolution",
      description:
        "Foster trust among your users by providing a robust mechanism for resolving disputes swiftly and fairly, enhancing the overall user experience.",
    },
    {
      number: 7,
      title: "Trading Bot Support",
      description:
        "Empower users with automated trading strategies by integrating trading bots, allowing for more efficient and strategic trading activities.",
    },
    {
      number: 8,
      title: "Elevated UI/UX Design",
      description:
        "Delight your users with an intuitive and visually appealing interface, offering a seamless trading experience across various devices.",
    },
    {
      number: 9,
      title: "Payment Gateway Integration",
      description:
        "Enable easy transactions with secure and efficient payment gateways, guaranteeing smooth deposits and withdrawals for users.",
    },
    {
      number: 10,
      title: "Multi-Signature Wallets",
      description:
        "Enhance security measures by incorporating multi-signature wallets, providing added protection for users' digital assets.",
    },
    {
      number: 11,
      title: "Swift Trading Matching Engine",
      description:
        "Execute trades efficiently with our high-speed trading matching engine, ensuring optimal performance even during peak trading hours.",
    },
    {
      number: 12,
      title: "Customize Trading Interfaces",
      description:
        "Cater to diverse user preferences with customizable trading interfaces, offering basic, advanced, and pro trading options tailored to individual needs.",
    },
  ];

  return (
    <section className="wazirx_feature_section">
      <Container>
        <Row className="mb_50">
          <h2 className="text-center">
            Cutting-Edge Features of Our WazirX Clone Script
          </h2>
        </Row>
        <Row className="rg_50">
          {featuresList.map((ele, index) => {
            return (
              <Col
                xxl={3}
                lg={4}
                sm={6}
                className="d-flex align-items-center justify-content-center"
              >
                <Card className="wazirx_script_card" key={index}>
                  <Card.Header>{ele.title}</Card.Header>
                  <Card.Text>{ele.description}</Card.Text>
                  <div className="wazirx_feature_count">{ele.number}</div>
                </Card>
              </Col>
            );
          })}
        </Row>
      </Container>
    </section>
  );
};

export default WazirxFeatures;
